    p Cuando se aplique un cambio de una poltica contable se har de forma retroactiva, como si la nueva poltica contable se hubiese aplicado siempre. Cuando sea impracticable determinar los efectos en cada periodo especfico de un cambio en una poltica contable sobre la informacin comparativa para uno o ms periodos anteriores para los que se presente informacin, la entidad aplicar la nueva poltica contable a los importes en libros de los activos y pasivos al principio del primer periodo para el que sea practicable la aplicacin retroactiva, el cual podra ser el periodo actual.

