      h2 1.2 Instrumentos financieros

    p.mb-5 Para la ejecucin de los instrumentos financieros es necesario conocer algunos elementos que son esenciales para el desarrollo e implementacin de los mismos, estos elementos sern abordados a continuacin.
    
