    .h5.mt-5.mb-4 Conceptos

    p.mt-5 <b>Costo amortizado:</b> es el <b>importe al que inicialmente fue valorado un pasivo financiero,</b> menos los <b>pagos</b> de principal que se hubieran producido, <b>menos los intereses correspondientes al perodo calculados</b> con la tcnica del tipo de inters efectivo. (Super contable Sage, s.f p.9)
    br
    p.mb-5 <b>Mtodo de la tasa de Inters Efectiva:</b> es un mtodo para calcular el costo amortizado de un pasivo, calculando la tasa de descuento para igualar los flujos de efectivo, en el periodo cero o periodo de medicin, es decir es la tasa que se le aplicar al instrumento financiero para calcular los intereses. Tomado de: Actualcese (2017, 17 de mayo).
    hr.mt-5.mb-5
    
