        .col-12
            ol.lista-ol--cuadro
              li 
                .lista-ol--cuadro__vineta
                  span b
                h5 Pasivo financiero

    p.mb-4 Es cualquier pasivo que represente una obligacin contractual de entregar dinero u otro activo financiero a otra entidad o de intercambiar activos financieros o pasivos financieros con otra entidad o un contrato que sea liquidado utilizando los instrumentos de patrimonio propio de la entidad, debe ser medido al costo amortizado utilizando la tcnica del inters efectivo sin embargo, si es para negociar incluidos los instrumentos derivados se mide a valor razonable con cambios en el estado de resultados. (Instrumentos financieros NIC 32 s.f p.9)
    br
    p.mb-5 <b>Valor Razonable:</b> es la intermediacin efectiva que se hace a travs de la oferta y demanda de forma libre, dicho de otra forma, es la interaccin de un comprador y un vendedor, que se obligan y comprometen a transar una cantidad de bien determinando un precio y unas condiciones especficas en un mercado libre.


