      li 
        i.fas.fa-check
        p <b>Medicin Posterior:</b> Se genera en los momentos futuros al reconocimiento inicial y comprende los ajustes propios del instrumento, dependiendo del modelo de negocio que implic su reconocimiento como para negociar o para la venta, por ejemplo, de tal manera que cuando se presente la informacin financiera los instrumentos estn valorados correctamente.
       
