    #t_1_4.titulo-segundo
      h2 1.4 Pasivos contingentes
    p Es una obligacin posible porque depende de que en el futuro ocurra algo que est fuera del alcance de la empresa para que sea reconocido el compromiso, por lo que los pasivos contingentes no se reconocen, pero s se revelan.
      
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-6
          img.img-a.my-3(src="@/assets/curso/images/tema1/img_5.svg" alt="", style="width:500px; display:block; margin:auto 0;")
      .col-lg-6
          h6.mb-3 Provisiones:
          p.mb-3 Son obligaciones en las cuales no hay certeza en cuanto al monto ni en cuanto a la fecha para su cancelacin. 
          .cajon.color-secundario.p-4.mb-4
            .h5 Ejemplo
            p Una organizacin espera el resultado de una demanda interpuesta por competencia desleal, los abogados estiman que la empresa tiene una probabilidad alta de perder, el veredicto lo darn los jueces dentro de dos aos y se estima el valor de la indemnizacin en $3.000.000 de pesos.
          p.mt-4 El hecho econmico implica reconocer la provisin por valor de tres millones de pesos, pero como se estima para dentro de dos aos su reconocimiento, el da de hoy se debe medir a su valor presente utilizando una tasa de descuento de mercado.

