    #t_1_5.titulo-segundo
      h2 1.5 Diferencia entre pasivo financiero e instrumento de patrimonio
    
    p El emisor de un instrumento financiero lo clasificar en el momento de su reconocimiento como un pasivo financiero, un activo financiero o un instrumento de patrimonio de conformidad con el acuerdo contractual que protocoliza el instrumento. Una forma de distinguir un pasivo financiero de un instrumento de patrimonio es que exista una obligacin contractual que recae sobre una cualquiera de las partes de entregar activos o intercambiar pasivos y activos de manera desfavorable para el emisor.
    
    p Por ejemplo, si se trata de una accin preferencial, esta ser reconocida como un pasivo financiero si el emisor est obligado a reembolsar por una cantidad acordada.
    
    p.mb-5 En el caso de un instrumento con opcin de venta, es decir que el tenedor debe devolverlo al emisor a cambio de efectivo u otro activo se reconoce como un pasivo financiero.

