            h6.mb-2 Medicin Posterior
            br
            p Es la forma de reconocer en los estados financieros de manera posterior, el importe del instrumento con el reconocimiento de los ajustes correspondientes dependiendo de la forma como es reconocido y medido el ttulo.
            br
            p La medicin se efecta de acuerdo con el modelo de negocio del suscriptor. A valor razonable con cambios en el resultado del ejercicio, a costo amortizado con el inters efectivo y a valor razonable con cambios en el patrimonio de la sociedad
